import React, { Component } from "react";
import { withTranslation } from "react-i18next";
import { connect } from "react-redux";
import { compose } from "redux";
import { authActions } from "../../state/ducks/auth";
import { userActions } from "../../state/ducks/user";
import { Col, Row, Spinner, Container } from "react-bootstrap";
import { DropZone, AuthenticatedLink, ActionButton } from "../../components";
import { BASEURL, FILENAME_CONSTANTS, URL_CONSTANTS } from "../../config/constants";
import { serviceOrderActions } from "../../state/ducks/serviceOrder";
import ScreenNav from "../../components/ScreenNav";
import Swal from "sweetalert2";
//import "./styles.scss";

class ServiceRequests extends Component {
  constructor(props) {
    super(props);
    this.state = {
      excelUploaded: null,
      loading: false
    }
       this.sendFiles = this.sendFiles.bind(this);
       this.setExcelUploaded = this.setExcelUploaded.bind(this);
  }

  componentDidMount(){
    const {resetServiceOrder} = this.props;
    resetServiceOrder();
  }

  sendFiles(){
    const {excelUploaded} = this.state;
    this.setState({loading: true})
    this.props.sendServiceRequest({excelUploaded, 
      callback: ({ success }) => {
      this.setState({loading: false});
      if(success){
        this.props.history.push("/service-requests/preload");
      } else {
        Swal.fire("Oops... algo sali mal", "Vuelva a intentarlo ms tarde", "error");
      }
    }, });
    
  }
  
  setExcelUploaded(excelFile){
    this.setState({excelUploaded: excelFile});
  }

  render() {
    const { history, getHeaders, t } = this.props;
    const url = `${BASEURL}${URL_CONSTANTS.downloadPlanilla}`;
    let headers;
    getHeaders({
      callback: (h) => {
        headers = h;
      },
    });

    const {excelUploaded, loading} = this.state;

    return (
      <>
        <Container fluid>
          <Row className="mt-4">
            <Col>
              <h1>Solicitudes de Servicio</h1>
              <ScreenNav
                  history={history}
                  previousPage={t("home")}
                  previousUrlPage="/home"
                  currentPage={t("sideBar.services")}
                />
              
            </Col>
          </Row>
          <Row className="mt-3">
            <Col>
            <p className="d-inline-block mr-2">
                {t("serviceRequests.excelDownloadAclaration")}
              </p>{" "}
              <AuthenticatedLink
                url={url}
                authHeaders={headers}
                filename={FILENAME_CONSTANTS.planilla}
              >
                Descargar planilla modelo
              </AuthenticatedLink></Col>
          </Row>
          <Row className="justify-content-center mt-5">
            <Col xs={10} md={10}>
              <DropZone setExcelUploaded={this.setExcelUploaded} />
            </Col>
          </Row>
          {excelUploaded && (
        <div className="mt-4 row">
          <div className="col text-center">
            <ActionButton
              onClick={() => this.sendFiles()}
              width={30}
              disabled={loading}
            >
              {loading ? (
                <Spinner animation="border" variant="light" />
              ) : (
                "Confirmar"
              )}
            </ActionButton>
          </div>
        </div>
      )}
        </Container>
      </>
    );
  }
}
const mapDispatchToProps = ({ auth, user, serviceOrder }) => ({ auth, user, serviceOrder });
export default compose(
  connect(mapDispatchToProps, { ...authActions, ...userActions, ...serviceOrderActions }),
  withTranslation()
)(ServiceRequests);
