import React, { Component } from "react";
import { withTranslation } from "react-i18next";
import { connect } from "react-redux";
import { compose } from "redux";
import { authActions } from "../../state/ducks/auth";
import { userActions } from "../../state/ducks/user";
import { serviceOrderActions } from "../../state/ducks/serviceOrder";
import { Col, Row, Container } from "react-bootstrap";
import EditForm from "./components/EditForm";
import { ScreenNav } from "../../components";

//import "./styles.scss";

class ServiceRequestsEdit extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { t, history } = this.props;
    return (
      <Container fluid>
        <Row className="mt-5">
          <Col>
            <ScreenNav
              history={history}
              previousPage={t("serviceRequests.requestsPreload")}
              currentPage={t("serviceRequests.requestDetail")}
            />
          </Col>
        </Row>

        <Row className="mt-5">
          <Col>
            <EditForm {...this.props} />
          </Col>
        </Row>
      </Container>
    );
  }
}
const mapDispatchToProps = ({ auth, user, serviceOrder }) => ({
  auth,
  user,
  serviceOrder
});
export default compose(
  connect(mapDispatchToProps, {
    ...authActions,
    ...userActions,
    ...serviceOrderActions
  }),
  withTranslation()
)(ServiceRequestsEdit);
