import { Button } from "react-bootstrap";
import "./styles.scss";

const ActionButton = ({
  type,
  onClick,
  children,
  width,
  disabled,
  secondary,
}) => {
  return (
    <Button
      type={type}
      onClick={onClick}
      className={`${secondary ? "secondary-action-button" : "action-button"} `}
      style={{width: isNaN(width) ? `${width}` : `${width}%` }}
      disabled={disabled}
    >
      {children}
    </Button>
  );
};

export default ActionButton;
