import { serviceOrderTypes } from "../ducks/serviceOrder";
import { authActions } from "../ducks/auth";
const resetServiceOrder = ({ dispatch }) => (next) => (action) => {
    next(action);
    if (action.type !== serviceOrderTypes.RESET_SERVICE_ORDER) {
        return;
    }
    dispatch({type:serviceOrderTypes.RESET_SERVICE_ORDER_DO});
}

const sendServiceRequest = ({dispatch})=> (next) => (action) => {
    next(action);
    if (action.type !== serviceOrderTypes.SEND_SERVICE_REQUEST) {
        return;
    }
    const {excelUploaded,callback} = action.payload;
    const data = new FormData();
    data.append("file", excelUploaded[0]);
    dispatch({
      type: serviceOrderTypes.API_CALL,
      payload: {
        config: {
          method: "POST",
          url: `service-request/upload`,
          data: data,
        },
        authorization: true,
        onStart: () => authActions.startFetch(),
        onComplete: ({ data }) => {
          if (data.success) {
            callback({ success: true});
            dispatch({
              type: serviceOrderTypes.UPDATE_SERVICE_ORDER_LIST,
              payload: { serviceOrderList: data.data },
            });
            
          } else {
            callback({
              success: false,
              data: { message: "Ha ocurrido un error" },
            });
          }
        },
        onError: async (error) => {
        },
        onEnd: () => authActions.endFetch(),
      },
    });
}

const editServiceRequest = ({ dispatch }) => (next) => (action) => {
  next(action);
  if (action.type !== serviceOrderTypes.EDIT_SERVICE_ORDER) {
      return;
  }
  const data = action.payload;
   dispatch({type:serviceOrderTypes.EDIT_SERVICE_ORDER_DO, payload: data});
}

const deleteServiceRequest = ({ dispatch }) => (next) => (action) => {
  next(action);
  if (action.type !== serviceOrderTypes.DELETE_SERVICE_ORDER) {
      return;
  }
  const data = action.payload;
   dispatch({type:serviceOrderTypes.DELETE_SERVICE_ORDER_DO, payload: data});
}


const createServiceRequest = ({dispatch})=> (next) => (action) => {
  next(action);
  if (action.type !== serviceOrderTypes.CREATE_SERVICE_REQUEST) {
      return;
  }
  const {data, callback} = action.payload;

  dispatch({
    type: serviceOrderTypes.API_CALL,
    payload: {
      config: {
        method: "POST",
        url: `service-request/create`,
        data: data,
      },
      authorization: true,
      onStart: () => authActions.startFetch(),
      onComplete: ({ data }) => {
        if (data.success) {
          if(data.status === 200){
            dispatch({
              type: serviceOrderTypes.UPDATE_SERVICE_ORDER_LIST,
              payload: { serviceOrderList: data.data },
            });
          }
          callback(data);

        } else {
          callback({
            success: false,
            data: { message: "Ha ocurrido un error" },
          });
        }
      },
      onError: async (error) => {
      },
      onEnd: () => authActions.endFetch(),
    },
  });
}

const getCustomerByCuit = ({dispatch})=> (next) => (action) => {
  next(action);
  if (action.type !== serviceOrderTypes.GET_CUSTOMER_BY_CUIT) {
      return;
  }
  const {data, callback} = action.payload;
  dispatch({
    type: serviceOrderTypes.API_CALL,
    payload: {
      config: {
        method: "GET",
        url: `/services-sait/cuit?code=${data.code}`
      },
      authorization: true,
      onStart: ()=> authActions.startFetch(),
      onComplete: ({ data }) => {
        if (data.success) {
          callback({success:true, data: data.data});
        } else {
          callback({
            success: false,
            data: { message: "No se ha conseguido la informacin."}
          })
        }
      },
      onError: (error) => {
        if(error.response && error.response.status === 412) {
          callback({data:{message:"Este CUIT ya fue dado de Alta"}, success:false})  
        }else{
          callback({data:error, success:false})
        }
      },
      onEnd: () => authActions.endFetch()
    }
  })
}


const getQuery = ({dispatch})=> (next) => (action) => {
  next(action);
  if (action.type !== serviceOrderTypes.GET_QUERY) {
    return;
  }
  const { fromDate, toDate, requestId, voucher, delivery , callback} = action.payload;

  dispatch({
    type: serviceOrderTypes.API_CALL,
    payload: {
      config: {
        method: "GET",
        url: `service-request/query?request-id=${requestId}&voucher=${voucher}&delivery=${delivery}&fromDate=${fromDate}&toDate=${toDate}`
      },
      authorization: true,
      onStart: () => authActions.startFetch(),
        onComplete: ({ data }) => {
          callback({ success: true, data: data });
        },
        onError: async (error) => {
          callback({ success: false, data: error });
        },
        onEnd: () => authActions.endFetch(),
    }
  })
}

const getEnabledPlaces = ({dispatch})=> (next) => (action) => {
  next(action);
  if (action.type !== serviceOrderTypes.GET_ENABLED_PLACES) {
    return;
  }
   const {callback} = action.payload
  dispatch({
    type: serviceOrderTypes.API_CALL,
    payload: {
      config: {
        method: "GET",
        url: `enabled-places`
      },
      authorization: true,
      onStart: () => authActions.startFetch(),
        onComplete: ( {data} ) => {
          callback(data)
        },
        onError: async (error) => {
          console.error(error)
          callback({success: false});

          // callback({ success: false, data: error });
        },
        onEnd: () => authActions.endFetch(),
    }
  })
}

export default [resetServiceOrder, sendServiceRequest, editServiceRequest, createServiceRequest, deleteServiceRequest,getQuery, getCustomerByCuit, getEnabledPlaces];
